@import "fonts";

body {
    font-size: 16px;
    
    background-image: url(../img/bg.jpg);
    background-size: cover;
}

input, button, a {
  outline: none;
}

/* скрываем чекбокс */
#menu__toggle {
    opacity: 0;
  }
  /* стилизуем кнопку */
  .menu__btn {
    display: flex; /* используем flex для центрирования содержимого */
    align-items: center;  /* центрируем содержимое кнопки */
    position: relative;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
    margin-right: 20px;
  }
  /* добавляем "гамбургер" */
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 3px;
    background-color: #fff;
    border-radius: 5px;
  }
  .menu__btn > span::before {
    content: '';
    top: -6px;
  }
  .menu__btn > span::after {
    content: '';
    top: 6px;
  }
.hamburger-menu {
    display: flex;
    align-items: center;

    span {
        color: #fff;
        font-size: 16px;
        font-family: "Montserrat-Medium";
        text-decoration: underline;
        margin-bottom: 7px;
    }
}
  /* контейнер меню */
.menu__box {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 300px;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: center;
    background-color: #ECEFF1;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
    z-index: 4;
  }
  /* элементы меню */
  .menu__item {
    display: inline-block;
    padding: 12px 24px;
    color: #333;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  .menu__item:hover {
    background-color: #CFD8DC;
  }

